import React from 'react';
import PropTypes from 'prop-types';
import { Panel, ListItem, Button, Group, Div, Avatar, PanelHeader, Cell } from '@vkontakte/vkui';

const Home = ({ id, go, fetchedUser, handleClick, text, isShow }) => (
	<Panel id={id}>
		<PanelHeader>Читаем вместе</PanelHeader>
		{fetchedUser &&
		<Group title="Ответы на вопросы для Вас">
			<ListItem
				before={fetchedUser.photo_200 ? <Avatar src={fetchedUser.photo_200}/> : null}
				description={fetchedUser.city && fetchedUser.city.title ? fetchedUser.city.title : ''}
			>
				{`${fetchedUser.first_name} ${fetchedUser.last_name}`}
			</ListItem>
		</Group>}

		<Group>
			<Div>
				{/*<Button size="xl" level="2" onClick={go} data-to="persik">*/}
					{/*Show me the Persik, please*/}
				{/*</Button>*/}

                <Button size="xl" level="2" onClick={handleClick}>
                    Получить ответ на вопрос
                </Button>

                {isShow &&
				<Div><Div style={{color: '#8e8e93',lineHeight: '1.5',}}>{text}</Div></Div>
                }
			</Div>
            <Cell href="https://vk.com/testpool?w=wall-134304772_19099" target="_blank" expandable={true}
                  description="Наша группа ВКонтакте">Официальное сообщество</Cell>
		</Group>
	</Panel>
);

Home.propTypes = {
	id: PropTypes.string.isRequired,
	go: PropTypes.func.isRequired,
	fetchedUser: PropTypes.shape({
		photo_200: PropTypes.string,
		first_name: PropTypes.string,
		last_name: PropTypes.string,
		city: PropTypes.shape({
			title: PropTypes.string,
		}),
	}),
};

export default Home;
